.site-footer {
  background-color: #0e0e0e;
  color: #fff;
  padding: 2rem 1rem;
  font-family: 'Arial', sans-serif;
}

.site-footer__inner {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  gap: 2rem;
}

.site-footer__left,
.site-footer__center,
.site-footer__right {
  flex: 1;
  min-width: 250px;
}

.footer-logo {
  width: 100px;
  margin-bottom: 1rem;
}

.footer-nav__main a,
.footer-nav__secondary a,
.social-links a {
  display: inline-block;
  margin-right: 1rem;
  text-decoration: none;
  font-weight: bold;
  color: #fff;
}

.footer-nav__main {
  margin-bottom: 1rem;
}

.footer__icons img {
  width: 28px;
  height: 28px;
  margin: 0.5rem 0.3rem;
  border-radius: 50%;
}

input[type="email"] {
  background: #191919;
  border: none;
  padding: 0.5rem 1rem;
  color: white;
  margin-top: 0.5rem;
  width: 100%;
}

.social-links {
  margin-top: 1rem;
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
}